.text-big {
  font-size: 5.052rem;
  font-weight: var(--semi-bold-600);
  line-height: 5.8098rem;
}

.text-section-title {
  color: var(--dark);
  font-size: 2.843rem;

  font-weight: var(--semi-bold-600);
  /* line-height: 32px; */
}

.text-subsection {
  font-size: 21.33px;
  line-height: 32px;

  color: var(--dark);
  font-weight: var(--regular-400);
}

.text-regular-title {
  font-size: 1.6rem;
  line-height: 2.4rem;

  font-weight: var(--semi-bold-600);
}

.text-regular {
