.header {
  width: 100%;
}

.header-ctn {
  max-width: 1194px;
  margin: 0 auto;

  padding-top: 1.6rem;
  padding-left: 1.6rem;
  padding-right: 1.6rem;
}

/*

  NAV

*/

.nav {
  display: flex;
  justify-content: space-between;
}

.nav-right {
  list-style: none;

  display: flex;
  gap: 1.6rem;
}

/*

  HEADER INTRO

*/

.header-intro {
  
}

.header-intro-greetings {
  margin-top: 4.8rem;
}

.header-ctv-ctn {
  display: none;
}

.header-intro-skills {
  max-width: 36rem;
  margin-top: 3.2rem;

  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

@media only screen and (min-width: 375px) {
  
}

@media only screen and (min-width: 520px) {
  .header-ctn {
    padding-top: 3.2rem;
    padding-left: 3.2rem;
    padding-right: 3.2rem; 
  }
}

@media only screen and (min-width: 768px) {
  .header-ctn {
    padding-top: 3.2rem;
    padding-left: 4.8rem;
    padding-right: 4.8rem;
  }
}

@media only screen and (min-width: 1024px) {
  .header-intro {
    display: grid;
    grid-template-columns: 1fr auto;
  }

  .header-intro-greetings {
    margin-top: 8rem;
  }

  .header-intro-skills {
    margin-top: 21.5rem;
  }
}

@media only screen and (min-width: 1280px) {
  .header-ctn {
    padding-left: 6.4rem;
    padding-right: 6.4rem;
  }
}