.featured-work {
  margin-top: 10rem;
}

.work-collection {
  margin-top: 4.8rem;

  /* display: grid; */
  /* gap: 12rem; */
}

.mb-12 {
  padding-bottom: 12rem;
}

.mt-12 {
  padding-top: 12rem;
}

.work {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.work-wide {
  padding-top: 8rem;
  padding-bottom: 8rem;
}

.item-legend {
  max-width: 22rem;
  
  margin-right: 4.8rem;
  padding-bottom: 4.8rem;

  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.item-collection {
  width: 100%;

  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  gap: 3.2rem;
}

.item-img {
  width: 100%;
  max-width: 28.8rem;
}

.single-web-img {
  width: 100%;
  border-radius: 12px;
}

.image-container {
  min-width: 288px;
  min-height: 619px;
  position: relative;
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(232, 54%, 11%, .06);
  border-radius: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid var(--dark);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/*

  MEDIA

*/

@media only screen and (min-width: 375px) {
  .featured-work {
    margin-top: 12rem;
  }
}

@media only screen and (min-width: 520px) {
  .item-collection {
    gap: 6.4rem;
  }

  .item-legend {
    padding-bottom: 6.4rem;
  }
}

@media only screen and (min-width: 768px) {
  .featured-work {
    margin-top: 14rem;
  }
}

@media only screen and (min-width: 1024px) {
  .work-collection {
    margin-top: 6.4rem;
  }

  .work-wide {
    padding-top: 12rem;
    padding-bottom: 12rem;
  }

  .item-collection {
    width: auto;
  }
}

@media only screen and (min-width: 1280px) {
  .item-legend {
    padding-bottom: 8rem;
  }
}