.container {
  width: 100%;
  max-width: 1194px;

  margin: 0 auto;

  padding-left: 1.6rem;
  padding-right: 1.6rem;
}

.ctv {
  padding: 1.525rem 2.8rem;

  background: rgb(226, 245, 5);
  border-radius: 1000px;

  color: var(--dark);
  font-size: 1.9rem;
  font-weight: var(--regular-400);
  text-decoration: none;
}

.contact-link {
  width: 4.8rem;
  height: 4.8rem;

  background: #f2f2f2;
  border-radius: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-img-ctn-medium {
  width: 4.8rem;
  height: 4.8rem;

  position: relative;
}

.profile-img {
  width: 100%;
  
  border-radius: 100%;
  background: #f2f2f2;
}

.online-ping {
  position: absolute;
  top: .4rem;
  right: .4rem;
  z-index: 1; 
 
}

.blob {
	background: #81F129;
	border-radius: 100%;
	height: .8rem;
	width: .8rem;

	box-shadow: 0 0 0 0 rgba(194, 255, 97, 1);
	transform: scale(1);
	animation: pulse 2s infinite;
}

.w-full {
  width: 100%;
}

/*

  ANIMATION

*/

@keyframes pulse {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(194, 255, 97, 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(194, 255, 9, 0);
	}

	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(194, 255, 9, 0);
	}
}

/*

  MEDIA

*/

@media only screen and (min-width: 375px) {
  .contact-link {
    width: 5.6rem;
    height: 5.6rem;
  }

  .profile-img-ctn-medium {
    width: 5.6rem;
    height: 5.6rem;
  }
}

@media only screen and (min-width: 520px) {
  .container {
    padding-left: 3.2rem;
    padding-right: 3.2rem;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    padding-left: 4.8rem;
    padding-right: 4.8rem;
  }
}

@media only screen and (min-width: 1024px) {
  
}

@media only screen and (min-width: 1280px) {
  .container {
    padding-left: 6.4rem;
    padding-right: 6.4rem;
  }
}

/* Maybe remove this class */

.collection {
  width: 100%;

  display: grid;
  grid-gap: 5.6rem;
}